        const [aeResponse, deResponse, singleCellResponse] = await Promise.all([getAbsolueExpression(), getDifferentialExpression(), getSingleCellExpression()]);
        aeData.value = aeResponse.data;
        deData.value = deResponse.data;
        singleCellData.value = singleCellResponse.data;

        // Fetch tissue data (compressed)
        try {
          const tissueResponse = await axios.get('/data/tissueJson.json.gz', {
            responseType: 'arraybuffer'
          });
          const decompressed = pako.inflate(new Uint8Array(tissueResponse.data), {
